<template>
   <div class="login-form normal-text">
      <div id="slider-container" class="slider-container">
         <div class="form-container login flex-center">
            <form @submit.prevent="doLogin">
               <h1 class="title-text">Bem vindo de Volta</h1>
               <input type="email" v-model="email" placeholder="E-mail"/>
               <input type="password" v-model="passwd" placeholder="Senha"/>
               <div class="wrong-pass subt-text">
                  E-mail ou senha incorreta
               </div>
               <div class="div-entrar">
                  <button class="title-text">Entrar</button>
                  <div class="forgot-pass subt-text">
                     Ish, esqueci a senha
                  </div>
               </div>
            </form>
         </div>

         <div class="form-container signup flex-center">
            <form @submit.prevent="createAccount">
               <h1 class="title-text">Criar Conta</h1>
               <div class="name">
                  <input type="text" v-model="name" placeholder="Nome"/>
                  <input type="text" v-model="last_name" placeholder="Sobrenome"/>
               </div>
               <input type="email" v-model="reg_email" placeholder="E-mail"/>
               <input type="tel" v-model="reg_wpp" placeholder="WhatsApp"/>
               <div class="pass">
                  <input type="password" v-model="reg_passwd" placeholder="Senha"/>
                  <input type="password" v-model="reg_passwd2" placeholder="Confirmar Senha"/>
               </div>
               <div class="error-msg"> </div>

               <button class="title-text">Cadastrar</button>

            </form>
         </div>

         <div class="overlay-container">
      		<div class="overlay">
      			<div class="panel left">
      				<h1>Acessar minha Conta</h1>
      				<p>Para continuar logado, informe seu Login e Senha</p>
      				<button class="title-text" id="slider-login">Entrar</button>
      			</div>
      			<div class="panel right">
      				<h1>Novo por aqui?</h1>
      				<p>Cadastre-se e comece uma nova jornada com a gente!</p>
      				<button class="title-text" id="slider-signup">Registrar</button>
      			</div>
      		</div>
   	   </div>
      </div>

   </div>

</template>


<script>
   import axios from 'axios'

   export default {
      name: 'ModalLogin',
      data() {
         return {
            email: 'alexan@alexcell.com.br',
            passwd: 'lequim157',

            name: 'Alexan',
            last_name: 'Oliveira',
            reg_email: 'alexan.oss@hotmail.com',
            reg_wpp: '62984995220',
            reg_passwd: 'lequim157',
            reg_passwd2: 'lequim1572'
         }
      },
      methods: {
         async createAccount() {
            const register = {
               name: this.name,
               last_name: this.last_name,
               email: this.reg_email,
               wpp: this.reg_wpp.replace(/\D/g,''),
               passwd: this.reg_passwd,
               passwd2: this.reg_passwd2
            };

            if (register.passwd != register.passwd2) {
               document.querySelector('.error-msg').innerText = "Senhas n√£o conferem"
               setTimeout(() => {
                  document.querySelector('.error-msg').innerText = ""
                  return
               }, 3e3);
            }else {
               delete register["passwd2"]
               await axios.post('api/users', register)
            }

         },

         async doLogin() {
            const login = {
               email: this.email,
               password: this.passwd
            };

            console.log(login)

            await axios.post('api/login', login)

         }
      }
   }
</script>