import IMask from "imask";

document.addEventListener("DOMContentLoaded", () => {
   //Show Login Modal
   if (document.querySelector(".btn-login")) {
      document.querySelector(".btn-login").addEventListener("click", () => {
         document.querySelector(".work-login-modal").classList.add("active");
         document.querySelector(".work-form").style.display = "none";
      });

      document
         .querySelector(".work-login-modal")
         .addEventListener("click", (ev) => {
            if (ev.target.matches(".work-login-modal")) {
               document
                  .querySelector(".work-login-modal")
                  .classList.remove("active");
               document
                  .querySelector(".work-form")
                  .style.removeProperty("display");

               //Reset Login Modal
               setTimeout(() => {
                  document
                     .querySelector("#slider-container")
                     .classList.remove("active");
               }, 4e2);
            }
         });

      //Login-Signup Slider
      document.querySelector("#slider-signup").addEventListener("click", () => {
         document.querySelector("#slider-container").classList.add("active");
      });

      document.querySelector("#slider-login").addEventListener("click", () => {
         document.querySelector("#slider-container").classList.remove("active");
      });

      //Phone Mask
      document
         .querySelector("input[type=tel]")
         .addEventListener("keydown", (el) => {
            // debugger
            var key = el.keyCode;
            //  backspace   delete       Arrow <      Arrow >
            if (key != 8 && key != 46 && key != 37 && key != 39) {
               //  numPad 0    numPad 9
               if (key < 96 || key > 105)
                  if (key < 48 || key > 57) {
                     //  0           9
                     el.preventDefault();
                     return;
                  }
            } else if (
               el.target.selectionStart == 0 &&
               el.target.selectionEnd >= 9
            ) {
               return;
            }

            var maskOptions = {
               mask: "+{55} (00) {9} 0000-0000" /* ,
               internacional: '+000 000 000 000' */,
            };

            IMask(el.target, maskOptions);
         });
   }

   //Show Construction Area Modal
   document.querySelector(".topnav").addEventListener("click", (ev) => {
      if (ev.target.matches(".soon")) {
         document.querySelector(".work-login-modal").classList.add("active");
         document.querySelector(".login-form").style.display = "none";
      }
   });

   document
      .querySelector(".work-login-modal")
      .addEventListener("click", (ev) => {
         if (ev.target.matches(".work-login-modal")) {
            document
               .querySelector(".work-login-modal")
               .classList.remove("active");
            document
               .querySelector(".login-form")
               .style.removeProperty("display");
         }
      });
});
